.wcf__image-accordion {
  display: flex;
  height: 70vh;
}
.wcf__image-accordion.accordion-layout-horizontal {
  flex-direction: row;
}
.wcf__image-accordion.accordion-layout-vertical {
  flex-direction: column;
}
.wcf__image-accordion .accordion-item {
  position: relative;
  overflow: hidden;
  flex: 1;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
  transition: all 0.3s;
}
.wcf__image-accordion .accordion-item.accordion-hover-active {
  flex: 3;
}
.wcf__image-accordion .accordion-item:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s;
  background: rgba(0, 0, 0, 0.3);
}
.wcf__image-accordion .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transform: translateY(100px);
}
.wcf__image-accordion .accordion-hover-active:after {
  opacity: 1;
  visibility: visible;
}
.wcf__image-accordion .accordion-hover-active .content {
  transition: all 1s;
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.wcf__image-accordion .subtitle {
  margin-bottom: 10px;
}
.wcf__image-accordion .title {
  margin-bottom: 10px;
}
.wcf__image-accordion .wcf__btn {
  margin-top: 20px;
}